// Import necessary modules
import React from 'react';
import { BrowserRouter as Router, Route, Switch, Link } from 'react-router-dom';
import '../App.css'; // Ensure to style appropriately
import { useState, useEffect } from 'react';

// Define an array of tiles for easier management

// Define the Main Tiles Component
const TilesComponent = ({ tiles, isQuotesEnabled }) => {
  console.warn('aaaaaaaaaaaaa', isQuotesEnabled);

  const [quotes, setQuotes] = useState([]);
  const [currentQuoteIndex, setCurrentQuoteIndex] = useState(0);

  useEffect(() => {
    const storedQuotes = localStorage.getItem('quotes');
    if (storedQuotes) {
      const quotesArray = JSON.parse(storedQuotes);
      setQuotes(Array.isArray(quotesArray) ? quotesArray : [storedQuotes]);
    }
  }, []);

  useEffect(() => {
    if (quotes.length > 1) {
      const interval = setInterval(() => {
        let randomIndex;
        do {
          randomIndex = Math.floor(Math.random() * quotes.length);
        } while (randomIndex === currentQuoteIndex); // Ensure a new quote is picked

        setCurrentQuoteIndex(randomIndex);
      }, 10000); // Change quote every 10 seconds

      return () => clearInterval(interval);
    }
  }, [quotes, currentQuoteIndex]); // Include `currentQuoteIndex` to ensure rerender

  const currentQuote = quotes.length > 0 ? quotes[currentQuoteIndex] : '';
  return (
    <>
      <div className="tiles-container">
        {tiles.map((tile, index) => (
          <Link key={index} to={tile.path} className="tile">
            {tile.label}
          </Link>
        ))}
      </div>

      {isQuotesEnabled && (
        <div className="quotes-container">
          <p>{currentQuote || 'No quotes available.'}</p>
        </div>
      )}
    </>
  );
};

export default TilesComponent;
